@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movie-row {
    margin-bottom: 30px;
}

.movie-row-title {
    margin: 0 0 0 30px;
}

.movie-row-list {
    transition: all ease .3s;
}

.movie-row-listarea {
    overflow-x: hidden;
    padding-left: 30px;
    width: 100%;
}

.movie-row-item {
    width: 150px;
    display: inline-block;
}

.movie-row-item img {
    width: 100%;
    transition: ease .2s;
    transform: scale(0.9);
}

.movie-row-item img:hover {
    transform: scale(1);
    cursor: pointer;
}

.movie-row-left,
.movie-row-right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,.6);
    z-index: 998;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .5s;
}

.movie-row:hover .movie-row-left,
.movie-row:hover .movie-row-right {
    opacity: 1;
}

.movie-row-left {
    left: 0px;
}

.movie-row-right {
    right: 0px;
}


@media only screen and (max-width: 760px) {
    .movie-row-left, .movie-row-right {
        opacity: 1;
    }
}
.featured-movie {
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.featured-vertical {
    width: inherit;
    height: inherit;
    background-image: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured-horizontal {
    width: inherit;
    height: inherit;
    background-image: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 70px 0 100px 40px;
}

.featured-name {
    font-size: 60px;
    font-weight: bold;
}

.featured-info {
    margin-top: 15px;
    font-size: 18px;
    font-weight: bold;
}

.featured-points {
    color: #46d369;
}

.featured-points,
.featured-year,
.featured-seasons {
    display: inline-block;
    margin-right: 15px;
}

.featured-description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;  
}

.featured-buttons {
    margin-top: 15px;
}

.featured-watch-button,
.featured-favorite-button {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 15px 25px;
    border-radius: 5px;
    margin-right: 10px;
    border: none;
    opacity: 1;
    transition: ease .2s;
}

.featured-watch-button:hover,
.featured-favorite-button:hover {
    opacity: 70%;
}

.featured-watch-button {
    background: white;
    color: black;
}

.featured-favorite-button {
    background: #333;
    color: white;
}

.featured-genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media only screen and (max-width: 760px) {
    .featured-movie{
        height: 90vh;
    }

    .featured-name {
        font-size: 40px;
    }

    .featured-info {
        font-size: 16px;
    }

    .featured-description {
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }

    .featured-watch-button,
    .featured-favorite-button {
        font-size: 14px;
    }

    .featured-genres {
        font-size: 14px;
    }
}
header {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    z-index: 999;
    padding: 0 40px;
    background: transparent;
    transition: all ease .3s;
}

header.collapsed {
    background-color: #141414;
}

.header-logo,
.header-user-img {
    height: 30px;
}

.header-logo img,
.header-user-img img{
    height: 100%;
    width: auto;
}

.header-user-img  img{
    border-radius: 3px;
}

footer {
    margin: 50px 0;
    text-align: center;
}
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  font-family: 'Roboto', sans-serif;
  color: white;
  margin: 0;
}

.movie-list {
  margin-top: -100px;
}

.loading {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
  z-index: 99;
}
 
.loading img {
  width: 60%;
}
